:root {
  --font-body: Arial, sans-serif;
  --red-hat: "Red Hat Display", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --paleblue: hsl(225, 100%, 94%);
  --brightblue: hsl(245, 75%, 52%);
  --brightblueshadow: hsla(245, 75%, 52%, 0.2);
  --brightbluehover: hsl(244, 78%, 66%);
  --brightbluehovershadow: hsla(244, 78%, 66%, 0.2);
  --verypaleblue: hsl(225, 100%, 98%);
  --desaturatedblue: hsl(224, 23%, 55%);
  --darkblue: hsl(223, 47%, 23%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background: var(--paleblue);
  font-family: var(--red-hat);
  font-size: 16px;
  margin: 0;
  padding: 0;
}
@media (min-width: 90em) {
  body::before {
    content: "";
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    background-image: url("/images/pattern-background-desktop.svg");
  }
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
  text-align: center;
  margin-top: 5rem;
}
@media (min-width: 71.875em) {
  .container {
    margin-top: 6.25rem;
    padding: 0;
    transform: translate(0, 0);
  }
}

.card {
  background: var(--white);
  display: inherit;
  margin: 0 auto;
  margin-bottom: 1.25rem;
  max-width: 28rem;
  border-radius: 1.25rem;
  overflow: hidden;
  transition: 150ms all ease-in-out;
}
.card__hero {
  width: 100%;
}
.card__tittle {
  font-size: 1.5rem;
  font-weight: 900;
  text-align: center;
  color: var(--darkblue);
  margin-top: 1.875rem;
  margin-bottom: 1.25rem;
}
.card__description {
  color: var(--desaturatedblue);
  width: 80%;
  margin: 0 auto;
  margin-bottom: 1.25rem;
  line-height: 1.6;
  /* padding: 0 rem(70);
  margin-bottom: rem(20);

  @include breakpoint-down(small) {
      padding: 0 rem(30);
  } */
}
.card__plan {
  background: var(--verypaleblue);
  border-radius: 0.625rem;
  padding: 1.25rem;
  width: 85%;
  margin: 0 auto;
  margin-bottom: 1.5625rem;
  /* display: flex;
  justify-content: start;
  align-items: center; */
  display: grid;
  grid-template-columns: 25% 2fr 25%;
}
@media (min-width: 71.875em) {
  .card__plan {
    width: 80%;
  }
}
.card__plan-price {
  text-align: left;
  grid-column-start: 2;
  grid-column-end: 3;
  margin: auto 0;
  font-weight: 700;
}
.card__plan-price div {
  font-weight: 400;
  color: var(--desaturatedblue);
  margin-top: 0.3125rem;
}
.card__plan-button {
  margin: auto;
  grid-column-start: 3;
  grid-column-end: 4;
  color: var(--brightblue);
  text-decoration: underline;
}
.card__plan-button:hover {
  color: var(--brightbluehover);
  text-decoration: none;
}
.card__btn-proceed {
  display: block;
  font-size: 1rem;
  margin: 0 auto;
  width: 85%;
  border: 0;
  padding: 0.9375rem 0;
  border-radius: 0.625rem;
  background: var(--brightblue);
  color: white;
  font-weight: 700;
  box-shadow: 0 0.8125rem 0.9375rem var(--brightblueshadow);
  margin-bottom: 0.3125rem;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .card__btn-proceed {
    width: 80%;
  }
}
.card__btn-proceed:hover {
  box-shadow: 0 0.8125rem 0.9375rem var(--brightbluehovershadow);
  background: var(--brightbluehover);
}
.card__btn-cancel {
  display: block;
  margin: 0 auto;
  width: 85%;
  padding: 0.9375rem 0;
  font-weight: 700;
  margin-bottom: 1.25rem;
}
.card__btn-cancel a {
  color: var(--desaturatedblue);
}
.card__btn-cancel a:hover {
  color: var(--black);
}